
/** @record */
export function NgbTimeStruct() {}
/**
 * The hour, going from 0 to 23
 * @type {number}
 */
NgbTimeStruct.prototype.hour;
/**
 * The minute, going from 0 to 59
 * @type {number}
 */
NgbTimeStruct.prototype.minute;
/**
 * The second, going from 0 to 59
 * @type {number}
 */
NgbTimeStruct.prototype.second;
/**
 * Interface of the model of the NgbTimepicker directive
 */
export interface NgbTimeStruct {
  /**
   * The hour, going from 0 to 23
   */
  hour: number;

  /**
   * The minute, going from 0 to 59
   */
  minute: number;

  /**
   * The second, going from 0 to 59
   */
  second: number;
}
